.card {
  background: #fff;
  border-radius: 2px;
  height: 225px;
  margin: 1rem;
  position: relative;
  width: 225px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center;
}
.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: white;
}
.card > .img-container > img {
  width: 100%
}
.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
}
.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}
.card > .remove:hover {
  color: #d50000;
}


